*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: monospace;
    scroll-behavior: smooth;
    text-decoration: none;
}
header{
    width: 100vw;
    height: 120px;
   display: flex;
   background-color: white;
   justify-content: space-around;

}
body{
    width: 100vw;
    height: 100vh;
}
main{
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: max-content;
    flex-direction: column;
    gap: 5rem;
}

.Logo{
    width: 10%;
    align-items: center;
    height: 100%;
}
.info{
    display: flex;
    gap: 10%;
    align-items: center;
    width: 40%;
    justify-content: right;
}
.infos_itens{
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;
}
.text_frame{
    width: 70%;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5px;
    
}
.text_frame span{
    font-size: 28px;
    flex-wrap: wrap;
    width: 100%;
  
}
.text_frame span::after{
    content: "";
    background: black;
    padding: 1.5px;
    position: relative;
    bottom: 5px;
    animation: bar .8s  infinite;
}
.text_frame p{
    margin-top: 15px;
}

.skills{
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
    padding: 16px;
}

.card_body{
    width: 300px;
    height: 350px;
    -webkit-box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    -moz-box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    gap: 0.9rem;
    position: relative;
    text-align: center;
    cursor: pointer;
}
.project_card img,
.project_card span,
.project_card img,
.project_card span,
.project_card p, 
.project_card i:hover{
    
}
.option_card{
    display: flex;
    z-index: 5;
    width: inherit;
    height: 30%;
    bottom: 0;
    position: absolute;
    padding: 10px;
    align-items: center;
    cursor: auto;
    gap: 10px;
    backdrop-filter: blur(5px);
}
.preview_code,.preview{
    flex: 1;
    border-radius: 30px;
    height: max-content;
    padding: 16px 8px ;
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
   background-color: black;
   color: white;
   transition: all .2s ease;
}
.preview:hover, .preview_code:hover{
    scale: 1.1;
}
.card_body span{
    flex: 1;
    font-size: 16px;
}
.card_body p{
    flex: 1;
}  
.card_body i{
    position: absolute;
    left: 10%;
    bottom: 10%;
    font-weight: 800;
}
.project{
    width: inherit;
    height: max-content;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
}
.project_cards{
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    width: inherit;
    height: inherit;
    justify-content: center;
}


@keyframes bar {
    50%{
        opacity: 0;
    }
    
}